@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(112, 115, 132);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  position: relative;
  background-color: #151D3C;
  /* background-color: rgba(68, 80, 139, 1); */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  /* padding-top: 50px; */
}


.title_container {
    position: relative;
    height: 800px;
    width: 1320px;
    background-color: #151D3C;
    /* border-radius: 67px 67px 0 0; */
    overflow: hidden;
}

.title_container h1 {
    position: absolute;
    width: 276px;
    height: 168px;
    left: 160px;
    top: 150px;
  
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 72px;
    line-height: 84px;
    letter-spacing: 0.2em;
    text-align: center;
    text-shadow: 1px 1px 1px black;
  
    color: white;
    z-index: 1;
}

.title_container #main-portrait {
    position: absolute;
    height: 700px;
    bottom: 0px;
    left: 400px;
    z-index: 1;
}

.title_container #rect1 {
    position: absolute;
    bottom: 230px;
    left: 230px;
    width: 107px;
    height: 12px;
    background-color: #021DA8;
}

.title_container #diamond {
    position: absolute;
    height: 200px;
    width: 200px;
    border-radius: 20px;
    transform: rotate(-30deg);
    top: 100px;
    right: 100px;
    background: rgba(255, 198, 26, 0.8);
    z-index: 0;
    animation: spin 20s infinite linear;
}
  
@keyframes spin {
    from { 
        transform: rotateZ(0deg) rotate(-45deg); 
    }
    to {
        transform: rotateZ(360deg) rotate(-45deg); 
    }
}

.title_container #diamond2 {
    position: absolute;
    height: 400px;
    width: 400px;
    border-radius: 50px;
    top: 80px;
    left: 80px;
    background: rgba(255, 198, 26, 0.8);
    z-index: 0;
    animation: spin2 30s infinite linear;
}

@keyframes spin2 {
    from { 
        transform: rotateZ(360deg) rotate(-45deg); 
    }
    to {
        transform: rotateZ(0deg) rotate(-45deg); 
    }
}



  
.social-container {
    position: absolute;
    left: 175px;
    bottom: 150px;
    width: 220px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    z-index: 1;
}

.social-container a {
    height: 64;
    width: 64;
    transition: all 0.3s ease-in-out
}

.social-container a:hover {
    transform: scale(1.2);
}
.intro-container {
    position: absolute;
    right: 100px;
    top: 200px;
    color: white;
    font-family: Roboto;
}

.intro-container #top {
    font-size: 18px;
    color: white;
}
    
.intro-container #middle {
    width: 400px;
    font-size: 48px;
    letter-spacing: 0.1em;
    color: white;
}

.intro-container #bottom {
    width: 410px;
    font-size: 18px;
    font-style: normal;
    font-weight: normal;
    letter-spacing: 0.1em;
    line-height: 30px;
    color: white;
}

.intro-container a {
    font-size: 48px;
    color: rgba(192, 201, 249, 1);
}

.nav-bar {
    position: absolute;
    top: 35px;
    right: 50px;
    width: 300px;
    z-index: 2;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.nav-bar a {
    color: white;
    font-family: 'Roboto';
    font-size: 18px;
    text-decoration: none;
    transition: all 0.3s;
}

.nav-bar a:hover {
    transform: scale(1.1);
    color: rgb(20, 197, 220)
} 

.projects-container {
    position: relative;
    width: 1320px;
    background-color: #747CC6;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.project-card {
    width: 300px;
    height: 350px;
    background: linear-gradient(to bottom, #ABB2F1, #b9abf1);
    border-radius: 38px;
    margin: 50px;
    box-shadow: 10px 10px 4px rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    transition: transform 0.3s ease-in-out;
}

.project-card:hover {
    transform: scale(1.2);
}

.project-card .title {
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.project-card .links-list,
.project-card .techs-list {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.project-card .links-list a {
    text-decoration: none;
    font-weight: bold;
    font-size: 24px;
    color: white;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.50);
    transition: transform .3s ease-in-out;
}

.project-card .links-list a:hover {
    transform: scale(1.2)
}
.contact-container {
    width: 1320px;
    height: 600px;
    margin-bottom: 50px;
    background-color: #151D3C;
    border-radius: 0 0 67px 67px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact-form {
    position: relative;
}

.contact-form .input-div {
    position: relative;
}
.contact-form .input-div input{
    width: 400px;
    padding: 10px;
    font-size: 18px;
    background: transparent;
    color: white;
    border-radius: 0;
    border: none;
    border-bottom: 3px solid white;
}

.contact-form .input-div input:focus {
    outline: none;
}

.contact-form .input-div label {
    position: absolute;
    top: 20px;
    left: 10px;
    color: #fff;
    font-size: 1.2em;
    pointer-events: none;
    transition: .5s;
}

.contact-form .input-div .active {
    color: rgb(20, 197, 220);
    top: 0px;
    left: 5px;
    font-size: 14px;
}

.contact-form label {
    position: relative;
    left: 10px;
    color: white;
    font-size: 18px;
}
.contact-form textarea {
    font-family: 'Roboto';
    font-size: 18px;
    resize: none;
    width: 400px;
    height: 200px;
    padding: 10px;
    color: white;
    border: 2px solid white;
    border-radius: 5px;
    background: transparent;
}


